require('./plugins');
var Backbone = require('backbone');
var Marionette = require('backbone.marionette');



// start the marionette inspector
if (window.__agent) {
  window.__agent.start(Backbone, Marionette);
}

var Application = require('./application/application.js');

var app = new Application();

window.io.connect();
console.log(app.socket);


app.module('modal', {
  moduleClass: require('./modal/module.js'),
  container: app.layout.overlay
});

app.module('header', {
  moduleClass: require('./header/module.js'),
  container: app.layout.header
});

app.module('flashes', {
  moduleClass: require('./flashes/module.js'),
  container: app.layout.flashes
});

app.module('index', {
  moduleClass: require('./index/module.js'),
  container: app.layout.content
});

app.module('colors', {
  moduleClass: require('./colors/module.js'),
  container: app.layout.content
});

app.module('books', {
  moduleClass: require('./books/module.js'),
  container: app.layout.content
});

app.module('exceptions', {
	moduleClass: require('./exceptions/module.js'),
	container: app.layout.content
});

Backbone.history.start();
