var Radio = require('backbone.radio');
var Module = require('./../common/module.js');
var Router = require('./router');

module.exports = Module.extend({
	initialize: function() {
		this.router = new Router(this.options);

		Radio.command('header', 'add', {
			name: 'Exceptions',
			path: 'exeptions',
			type: 'primary'
		});
	}
});
